.about-page {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  font-family: "Geologica", sans-serif;
  font-family: "Nunito Sans", sans-serif;
  font-family: "Rajdhani", sans-serif;
}
.about-info h2 {
  font-size: 2.6rem;
}
.about-info__lil {
  font-size: 3.2rem;
  text-align: center;
}
.about-info p {
  font-size: 1.8rem;
  color: rgba(0, 0, 0, 0.418);
}
.about-info__container {
  width: 100%;
  padding: 3em 0;
  display: flex;
  gap: 1rem;
  justify-content: center;
  align-items: center;
}

.about-info {
  width: 50%;

  height: 70%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 1rem;
}
.about-info p {
  width: 60%;
  text-align: center;
}

.about-icon__container {
  display: flex;

  gap: 4rem;
}
.about-icons {
  margin-top: 2rem;
  font-size: 3.6rem;
}
.test {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.test span {
  font-size: 2rem;
}
.about-icons {
  color: orange;
}
@media (width <= 600px) {
  .about-info__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  .about-info {
    width: 90%;
  }
  .about-info h2 {
    font-size: 2.2rem;
    padding-bottom: 0.2em;
  }
  .about-info__lil {
    font-size: 2.4rem;
    padding-bottom: 1em;
  }
  .about-info p {
    font-size: 1.2rem;
    width: 80%;
  }
}
