}
.car-info__icons {
  display: flex;
  justify-content: end;
  text-align: end;
  font-size: 3rem;
  gap: 2rem;
  cursor: pointer;
  margin: 1rem;
  height: 10vh;
}
.section-car-info__container {
  font-family: "Geologica", sans-serif;
  font-family: "Nunito Sans", sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  /* height: 70vh; */
  width: 100%;
  /* background-color: red; */
}
@media (width <= 600px) {
  .second-section__filler {
    width: 100%;
    margin-bottom: 6em;
  }
}
